import { mapState } from 'vuex'
export default {
	data() {
		return {
			list1: [{id: 1, value: '男'},{id: 2, value: '女'}],
			list2: ['69前','70后','80后','90后','00后'],
			list3: [],
			sexValue: '',
			birthValue: '',
			occupationValue: '',
			index1: -1,
			index2: -1,
			index3: -1
		}
	},
	computed: {
		...mapState(['userInfo']),  //对全局变量hasLogin进行监控
	},
	created() {
		this.$http.get({
			url: '/v1/rest/public/findDictList',
			data:{
				skey: 'occupation',
			}
		}).then(resp => {
			console.log(resp)
			if(resp.status == 200) {
				this.list3 = resp.data
			}
		})
	},
	onLoad() {
		
	},
	methods: {
		select1(item,index) {
			this.sexValue = item.id
			this.index1 = index
		},
		select2(item,index) {
			this.birthValue = item
			this.index2 = index
		},
		select3(item,index) {
			this.occupationValue = item.value
			this.index3 = index
		},
		// 提交
		handleSure() {
			if(this.index1 < 0) {
				uni.showToast({
					title: '请选择性别！',
					duration: 2000,
					icon: 'none'
				});
			}else if(this.index2 < 0) {
				uni.showToast({
					title: '请选择年龄段！',
					duration: 2000,
					icon: 'none'
				});
			}else if(this.index3 < 0) {
				uni.showToast({
					title: '请选择职业！',
					duration: 2000,
					icon: 'none'
				});
			}else {
				const params = {
					userId: this.userInfo.userId,
					sex: this.sexValue,
					ageGroup: this.birthValue,
					occupation: this.occupationValue,
				}
				console.log(params)
				this.$http.post({
					url: '/v1/rest/login/registerLabelSaveOne',
					data: params,
				}).then(resp => {
					console.log(resp)
					if(resp.status == 200) {
						uni.redirectTo({
							url: '/pages/userInfo/selectionTags/selectionTags?sex=' + this.sexValue
						})
					}
				})
			}
		}
	}
}
